ocs:'5'\r\n        }),\r\n        methods: {\r\n            createChart(chartId, chartData) {\r\n                var canvas = document.getElementById(chartId);\r\n                var ctx = canvas.getContext(\"2d\");\r\n                const myChart = new Chart(ctx, {\r\n                    type: chartData.type,\r\n                    data: chartData.data,\r\n                    options: chartData.options,\r\n                });\r\n            },\r\n\r\n            actividades:function () {\r\n                window.location.href = '/actividades/'+this.practica.idpractica+'/list';\r\n            },\r\n\r\n            documentos:function () {\r\n                window.location.href = '/documentos/'+this.practica.idpractica+'/list';\r\n            },\r\n            gettotaldocs: function () {\r\n                axios.get(window.location.origin+'/api/totaldocs',{\r\n                        params:{'carrera':this.practica.estudiante.carrera.idcarrera}\r\n                }).then((response)=>{\r\n                    this.totaldocs=response.data;\r\n\r\n                }).catch(function (error) {\r\n                    console.log(error);\r\n                });\r\n            }\r\n        },\r\n        mounted() {\r\n\r\n            const data = {\r\n                type: 'doughnut',\r\n                data: {\r\n                    labels: [\"Completo\", \"Incompleto\"],\r\n                    datasets: [\r\n                        {\r\n                            label: \"%Proyecto\",\r\n                            backgroundColor: [\"#e843b9\", \"#ddd\"],\r\n                            data: [this.suma,(this.practica.horaspractica-this.suma)]\r\n                        }\r\n                    ]\r\n                },\r\n                options: {\r\n                    title: {\r\n                        display: true,\r\n                        text: '% Avance'\r\n                    },\r\n                    responsive: true,\r\n                    cutoutPercentage: 90,\r\n                    legend: {\r\n                        display: false\r\n                    },\r\n                    tooltips: {\r\n                        callbacks: {\r\n                            label: function(tooltipItem, data) {\r\n                                var dataset = data.datasets[tooltipItem.datasetIndex];\r\n                                var meta = dataset._meta[Object.keys(dataset._meta)[0]];\r\n                                var total = meta.total;\r\n                                var currentValue = dataset.data[tooltipItem.index];\r\n                                var percentage = parseFloat((currentValue/total*100).toFixed(1));\r\n                                return currentValue + ' (' + percentage + '%)';\r\n                            },\r\n                            title: function(tooltipItem, data) {\r\n                                return data.labels[tooltipItem[0].index];\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n            };\r\n            const data2 = {\r\n                type: 'doughnut',\r\n                data: {\r\n                    labels: [\"Completo\", \"Incompleto\"],\r\n                    datasets: [\r\n                        {\r\n                            label: \"%Documentos\",\r\n                            backgroundColor: [\"#3e95cd\",\"#ddd\"],\r\n                            data: [this.docs,(this.totaldocs-this.docs)]\r\n                        }\r\n                    ]\r\n                },\r\n                options: {\r\n                    title: {\r\n                        display: true,\r\n                        text: '% Documentos'\r\n                    },\r\n                    responsive: true,\r\n                    cutoutPercentage: 90,\r\n                    legend: {\r\n                        display: false\r\n                    },\r\n                    tooltips: {\r\n                        callbacks: {\r\n                            label: function(tooltipItem, data) {\r\n                                var dataset = data.datasets[tooltipItem.datasetIndex];\r\n                                var meta = dataset._meta[Object.keys(dataset._meta)[0]];\r\n                                var total = meta.total;\r\n                                var currentValue = dataset.data[tooltipItem.index];\r\n                                var percentage = parseFloat((currentValue/total*100).toFixed(1));\r\n                                return currentValue + ' (' + percentage + '%)';\r\n                            },\r\n                            title: function(tooltipItem, data) {\r\n                                return data.labels[tooltipItem[0].index];\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n            };\r\n\r\n            this.gettotaldocs();\r\n            this.createChart('avancechart', data);\r\n            this.createChart('documentoschart', data2);\r\n            console.log(this.docs);\r\n\r\n\r\n        }\r\n    }\r\n</script>\r\n\r\n<style scoped>\r\n    .donut-inner {\r\n        text-align: center;\r\n        margin-top: -28%;\r\n        margin-bottom: 30%;\r\n    }\r\n    .donut-inner2 {\r\n        text-align: center;\r\n        margin-top: -28%;\r\n        margin-bottom: 12%;\r\n    }\r\n    .donut-inner h5 {\r\n        margin-bottom: 5px;\r\n        margin-top: 0;\r\n    }\r\n    .donut-inner span {\r\n        font-size: 5px;\r\n    }\r\n</style>"],"sourceRoot":""}]);

